export const addData = (store,payload)=>{
  const newValue = store.state.data.push(payload)
  store.setState({newValue})
}

export const deleteData = (store,payload)=>{
  let index = store.state.data.indexOf(payload)
  if(index > -1){
    store.state.data.splice(index,1)
  }
  store.setState(store.state.data)
}