import React from "react";
import useGlobal from "../store";

export default function Table() {
  const [globalState] = useGlobal();

  return (
    <table>
      <thead>
        <tr>
          <th>#</th>
          <th>NAME</th>
          <th>AGE</th>
          <th>GENDER</th>
        </tr>
      </thead>
      <tbody>
        {globalState.data.length ? (
          globalState.data.map((val, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{val.name}</td>
              <td>{val.age}</td>
              <td>{val.gender}</td>
            </tr>
          ))
        ) : (
          <tr>
            <td colSpan="4" className="text-center">
              No Data
            </td>
          </tr>
        )}
      </tbody>
    </table>
  );
}
