import "./styles.css";
import useGlobal from "./store";
import { useState } from "react";
import Table from "./component/Table";

export default function App() {
  const [values, setValues] = useState({
    name: "",
    age: 0,
    gender: "male"
  });
  const [globalState, globalAction] = useGlobal();

  const add = (e) => {
    e.preventDefault();
    globalAction.addData(values);
    setValues({ name: "", age: 0, gender: "male" });
  };

  const eventFactory = (key) => (event) => {
    setValues({ ...values, [key]: event.target.value });
  };

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>

      <form onSubmit={add}>
        <input value={values.name} onChange={eventFactory("name")} />
        <input value={values.age} onChange={eventFactory("age")} />
        <input value={values.gender} onChange={eventFactory("gender")} />
        <input type="submit" />
      </form>

      <br />

      <Table />
    </div>
  );
}
